@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;1,300&display=swap");
.homePage {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    margin: 0rem 24% 0rem 24%;
}

.subSection {
    position: absolute;
    top: 16rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 61.25rem;
    height: 41.375rem;
}

.subSection::before {
    content: "";
    display: block;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 35rem;
    opacity: 25%;
    background-color: rgb(255, 255, 255);
    /* z-index: 10; */
}

#video {
    position: fixed;
    z-index: -1;
    width: 100%;
}

.homeCaption {
    line-height: 45px;
    margin-top: 2.5rem;
    font-size: 23px;
    font-family: "Lato", sans-serif;
    text-align: center;
    margin-bottom: 4rem;
}

.homePage_Featured {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.homePageMainSection {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.homePage_Featured {
    margin-top: 5rem;
}
