.AreYou {
    margin: 0rem 18% 0rem 18%;
    margin-top: 4rem;
}
.areYouMain {
    display: flex;
    /* align-items: center; */
    justify-content: center;
    flex-direction: row;
    gap: 4.5rem;
}
.leftContainer {
    width: 40%;
    overflow: hidden;
}
.rightContainer {
    width: 60%;
}

.subHeader {
    text-transform: uppercase;
    font-style: normal;
    font-size: 18px;
    font-weight: light;
    margin-bottom: 1.25rem;
}

.rightContainerGrid {
    margin-top: 2.5rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
}
